.box {

  &-list {
    box-sizing: border-box;
    margin: 0;
    margin-left: 20px;
    padding: 0;

    &-item {
      border-left: 2px solid @primary-color;
      box-sizing: border-box;
      margin: 0;
      padding: 5px 10px;
      transition: 0.2s margin;

      &:hover {
        margin-left: 2px;
      }
    }
  }
}

@primary-color: #00796B;@theme-color: #00796B;@tint-color: #009688;@light-color: #B2DFDB;@gray: #BDBDBD;@dark-gray: #757575;@light-gray: #e8e8e8;@black: #212121;@white: #f0f0f0;