.item {
  box-sizing: border-box;
  margin: 7px;
  padding: 5px;
  height: 105px;
  overflow: hidden;
  border-bottom: 3px solid @light-gray;
  transition: 0.3s all;

  &:hover {
    border-bottom: 3px solid @primary-color;
  }
}

.itemText {
  font-size: 12px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.itemDetail {
  font-size: 13px;
}

@primary-color: #00796B;@theme-color: #00796B;@tint-color: #009688;@light-color: #B2DFDB;@gray: #BDBDBD;@dark-gray: #757575;@light-gray: #e8e8e8;@black: #212121;@white: #f0f0f0;